import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Modal, message, Menu, Icon } from 'antd';
import { getRestudyStudent } from 'services/classCreditEnrollment';
import FormInfoCommon from './FormInfoCommon';
import TableStudentStudyAndExamAgain from './TableStudentStudyAndExamAgain';
import RegisterStudyAgainPage from './RegisterStudyAgainPage';
import './style.scss';
import ListStudentRegisterdPage from './ListStudentRegisterdPage';

class WForcedStopSchoolPage extends Component {
  state = {
    showRegisterStudyAgainForm: false,
    showListRegisteredStudyAgainModal: false,
    searchStudentValues: {},
    tableData: [],
    totalResults: 0,
    selectedStudents: [],
    isUniqueSubjectId: false,
    isSearchingData: false,
    selectedRowKeys: [],
  };

  handleShowRegisterStudyAgainForm = (visible) => {
    const { showRegisterStudyAgainForm } = this.state;
    if (showRegisterStudyAgainForm !== visible) {
      this.setState({ showRegisterStudyAgainForm: visible });
    }
  }

  handleShowListRegisteredStudyAgain = (visible) => {
    const { showListRegisteredStudyAgainModal } = this.state;
    if (showListRegisteredStudyAgainModal !== visible) {
      this.setState({ showListRegisteredStudyAgainModal: visible });
    }
  }

  handleSearchForm = async (formValues) => {
    this.setState({
      searchStudentValues: formValues,
      isSearchingData: true,
      selectedStudents: [],
      isUniqueSubjectId: false,
      showRegisterStudyAgainForm: false,
      selectedRowKeys: [],
    });
    try {
      const res = await getRestudyStudent(formValues);
      if (res.data && res.data.items) {
        this.setState({ tableData: res.data.items, totalResults: res.data.totalResults });
      }
    } catch (err) {
      message.error('Có lỗi xãy ra!');
    } finally {
      this.setState({ isSearchingData: false });
    }
  }

  handleClearForm = () => {
    const { searchStudentValues } = this.state
    if (Object.keys(searchStudentValues).length > 0) {
      this.setState({
        searchStudentValues: {},
        tableData: [],
        totalResults: 0,
        showRegisterStudyAgainForm: false,
        isSearchingData: false,
        selectedStudents: [],
        isUniqueSubjectId: false,
        selectedRowKeys: [],
      });
    }
  }

  handleTableRowSelection = (selectedRowKeys, selectedRows) => {
    const isUniqueSubjectId = selectedRows.length > 0 && new Set(selectedRows.map(m => m.subjectId)).size === 1
    this.setState({ selectedStudents: selectedRows, isUniqueSubjectId, selectedRowKeys })
  }

  render () {
    const {
      showRegisterStudyAgainForm,
      searchStudentValues,
      tableData,
      totalResults,
      isSearchingData,
      selectedStudents,
      isUniqueSubjectId,
      selectedRowKeys,
      showListRegisteredStudyAgainModal
    } = this.state;

    return (
      <Card title="Danh sách sinh viên học lại, thi lại">
        <Row className="rowMargin">
          <Col span={24}>
            <Menu selectable={false} mode="horizontal">
              <Menu.Item key="registerStudyAgain" disabled={!isUniqueSubjectId} onClick={() => { this.handleShowRegisterStudyAgainForm(true); }} >
                <Icon type="highlight" theme="twoTone" twoToneColor="#ff3333" />
                Đăng ký học lại
              </Menu.Item>
              <Menu.Item key="listRegisteredStudyAgain" disabled={Object.keys(searchStudentValues).length === 0} onClick={() => { this.handleShowListRegisteredStudyAgain(true); }}>
                <Icon type="down-circle" theme="twoTone" twoToneColor="#6699ff" />
                Danh sách đã đăng ký học lại
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoCommon
              handleSearchForm={this.handleSearchForm}
              handleClearForm={this.handleClearForm}
              loading={isSearchingData}
              noOfStudents={totalResults}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableStudentStudyAndExamAgain
              dataSource={tableData}
              loading={isSearchingData}
              selectedRowKeys={selectedRowKeys}
              handleTableRowSelection={this.handleTableRowSelection}
            />
          </Col>
        </Row>
        <Modal
          title="Đăng ký học lại"
          visible={showRegisterStudyAgainForm}
          style={{ top: 20 }}
          footer={null}
          onCancel={() => this.handleShowRegisterStudyAgainForm(false)}
          maskClosable={false}
          forceRender
          className="modal-register-restudy"
          width="900px"
        >
          <RegisterStudyAgainPage
            key={`${searchStudentValues.yearEduId}_${searchStudentValues.semesterId}`}
            searchStudentValues={searchStudentValues}
            selectedStudents={selectedStudents}
            handleShowRegisterStudyAgainForm={this.handleShowRegisterStudyAgainForm}
          />
        </Modal>
        <Modal
          title="Danh sách đã đăng ký học lại"
          visible={showListRegisteredStudyAgainModal}
          style={{ top: 20 }}
          footer={null}
          onCancel={() => this.handleShowListRegisteredStudyAgain(false)}
          maskClosable={false}
          forceRender
          className="modal-registerd-restudy"
          width="1200px"
        >
          <ListStudentRegisterdPage
            key={`${searchStudentValues.yearEduId}_${searchStudentValues.semesterId}_${searchStudentValues.trainTypeId}_${searchStudentValues.courseId}`}
            searchStudentValues={searchStudentValues}
          />
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <WForcedStopSchoolPage />
  </Page>
);



