import React, { Component } from 'react';
import { Table } from 'antd';

class TableStudentStudyAndExamAgain extends Component {
  constructor (props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'Ký hiệu',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'Số tín chỉ',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'Năm học',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
      },
      {
        title: 'Học kỳ',
        dataIndex: 'semester',
        key: 'semester',
      },
      {
        title: 'Lần thi',
        dataIndex: 'noOfTest',
        key: 'noOfTest',
      },
      {
        title: 'Điểm thi lần 1',
        dataIndex: 'firstGrade',
        key: 'firstGrade',
      },
      {
        title: 'Điểm thi cao nhất',
        dataIndex: 'maxGrade',
        key: 'maxGrade',
      },
    ];
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    if (typeof this.props.handleTableRowSelection === 'function') {
      this.props.handleTableRowSelection(selectedRowKeys, selectedRows);
    }
  }

  render () {
    const { dataSource, selectedRowKeys } = this.props

    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    }
    return (
      <div>
        <Table
          bordered
          rowSelection={rowSelection}
          dataSource={dataSource}
          columns={this.columns}
          loading={this.props.loading}
          pagination={false}
        />
      </div>
    );
  }
}

export default TableStudentStudyAndExamAgain;



