import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getListSubject } from 'services/Subject';
import { getCollectTypes } from 'services/classCreditEnrollment';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
    this.props.handleClearForm();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.props.handleSearchForm(values);
    });
  };

  render () {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      classId: {
      },
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn học phần',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        </Row>
        <Row gutter={24}>
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} span={5} />
          <Col xs={24} sm={12} md={5}>
            <Form.Item label="Tổng hợp theo:">
              {getFieldDecorator('collectTypeId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Tổng hợp theo!',
                  },
                ],
                initialValue: 1
              })(
                <SelectAjax
                  service={getCollectTypes}
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={5}>
            <Form.Item label="Học phần:">
              {getFieldDecorator('subjectId', {})(
                <SelectAjax
                  service={getListSubject}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, yearEduId, semesterId }}
                  allowGetData={!!trainTypeId && !!facultyId && !!yearEduId && !!semesterId}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={12}>
            <span>Tổng số sinh viên: <b>{this.props.noOfStudents}</b></span>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }}>
            <BtnSearch onClick={this.handleSearch} loading={this.props.loading} />
            <BtnClear onClick={this.handleReset} loading={this.props.loading} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



